:root {
  --font-body: "Space Mono", monospace;
}

:root {
  --strong-cyan: hsl(172, 67%, 45%);
  --very-dark-cyan: hsl(183, 100%, 15%);
  --very-dark-cyan-two: hsl(183, 78%, 23%);
  --dark-grayish-cyan: hsl(186, 14%, 43%);
  --dark-grayish-cyan-two: hsl(184, 14%, 56%);
  --light-grayish-cyan: hsl(185, 41%, 84%);
  --light-grayish-cyan-two: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  padding-top: 3.125rem;
  background: var(--light-grayish-cyan);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  text-align: center;
  color: var(--dark-grayish-cyan);
  letter-spacing: 0.75rem;
  font-size: 1.125rem;
}

h5 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input {
  font-family: var(--font-body);
}

.container {
  padding: 1.25rem;
  max-width: 57.1875rem;
  margin: 0 auto;
  margin-top: 3.125rem;
  background: var(--white);
  border-radius: 1.25rem;
}
@media (min-width: 40em) {
  .container {
    padding: 1.875rem 1.875rem 1.9375rem 2.875rem;
    transform: translate(0, 0);
    display: flex;
    margin-top: 5.625rem;
  }
}

@media (min-width: 40em) {
  .first-section {
    flex: 1;
    margin-right: 1.4375rem;
  }
}
.first-section h5 {
  font-size: 0.75rem;
  color: var(--dark-grayish-cyan);
}
.first-section__label {
  position: relative;
}
.first-section__label::before {
  content: "";
  position: absolute;
  background: url(../images/icon-dollar.svg);
  background-repeat: no-repeat;
  top: 0.1875rem;
  left: 1.0625rem;
  width: 1.25rem;
  height: 1.25rem;
}
.first-section .inpu {
  background-color: var(--light-grayish-cyan-two);
  border: none;
  border-radius: 0.3125rem;
  padding: 0.25rem 1.0625rem;
  text-align: right;
  border: var(--white) solid 0.125rem;
  font-size: 1rem;
  letter-spacing: 0.0625rem;
  font-weight: 700;
  color: var(--very-dark-cyan);
}
.first-section .inpu:focus {
  outline: none;
  border: var(--strong-cyan) solid 0.125rem;
}
.first-section .bill {
  width: 100%;
  margin-top: 0.625rem;
}

.percent {
  margin-top: 1.5rem;
}
.percent .pgb {
  border: none;
  padding: 0.25rem 1.0625rem;
  cursor: pointer;
  border-radius: 0.3125rem;
  font-size: 1rem;
  font-family: var(--font-body);
  font-weight: 700;
  color: var(--white);
  background-color: var(--very-dark-cyan);
  transition: all 0.25s ease;
}
.percent .pgb:hover {
  background-color: var(--light-grayish-cyan);
  color: var(--very-dark-cyan);
}
.percent .pgb:disabled {
  cursor: not-allowed;
}
.percent .pgb:disabled:hover {
  background-color: var(--very-dark-cyan);
  color: var(--white);
}
.percent .pgb.clicked {
  background-color: var(--strong-cyan);
  color: var(--very-dark-cyan);
}
.percent__grid {
  margin-top: 0.625rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.9375rem;
}
.percent__grid--inpu {
  min-width: 3.125rem;
}
.percent__grid--inpu::placeholder {
  text-align: center;
}
.percent__grid--inpu:disabled {
  cursor: not-allowed;
}
@media (min-width: 26.25em) {
  .percent__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 40em) {
  .percent__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 50em) {
  .percent__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.percent__nop {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
}
.percent #percent__zero {
  color: #e60000;
  display: none;
}
.percent #percent__zero.dpr {
  display: unset;
}
.percent__label {
  position: relative;
}
.percent__label::before {
  content: "";
  position: absolute;
  background: url(../images/icon-person.svg);
  background-repeat: no-repeat;
  top: 0.1875rem;
  left: 1.0625rem;
  width: 0.9375rem;
  height: 0.9375rem;
}
.percent__label--inpu {
  margin-top: 0.625rem;
  width: 100%;
}
.percent__label--inpu:disabled {
  cursor: not-allowed;
}
.percent__label--inpu.dpr {
  border: #e60000 solid 0.125rem;
}

.ss {
  background-color: var(--very-dark-cyan);
  border-radius: 0.625rem;
  padding: 1.5625rem 1.25rem 1.25rem;
  margin-top: 1.5rem;
}
@media (min-width: 40em) {
  .ss {
    flex: 1;
    margin-left: 1.4375rem;
    position: relative;
  }
}
.ss__f {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ss__f:first-child {
  margin-bottom: 1.25rem;
}
.ss h5 {
  color: var(--white);
  font-size: 1rem;
}
.ss p {
  color: var(--dark-grayish-cyan);
  font-size: 0.75rem;
  font-weight: 700;
}
.ss__num {
  font-size: 1.375rem;
  font-weight: 700;
  color: var(--strong-cyan);
}
.ss__reset {
  width: 100%;
  border-radius: 0.3125rem;
  border: none;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--very-dark-cyan);
  padding: 0.625rem;
  margin-top: 1.25rem;
  background-color: var(--strong-cyan);
  cursor: pointer;
  transition: 0.25s ease all;
}
.ss__reset:hover {
  background-color: var(--light-grayish-cyan);
}
.ss__reset:disabled {
  background-color: var(--very-dark-cyan-two);
  cursor: unset;
}
.ss__reset:disabled:hover {
  cursor: not-allowed;
}
@media (min-width: 40em) {
  .ss__reset {
    width: 86%;
    position: absolute;
    bottom: 1.4375rem;
  }
}